@import url(<link href="https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,600,700&display=swap" rel="stylesheet">);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html{
  margin:0;
  padding:0;
  font-family: 'Josefin Sans', sans-serif;
  background: rgb(0, 51, 51);
}

*{
 box-sizing: border-box;
}



main{
  padding-top: 20vh;
  min-height: 100vh;
  display: grid;
  place-items: center;
}

button, .button{
    background-color: rgb(0, 42, 42);
    cursor: pointer;
    color: whitesmoke;
    min-width: 6rem;
    padding: .6rem;
    display: block;
    border: none;
    margin-bottom: 10px;
    font-size: .8rem;
    box-shadow:  2px 2px 7px rgb(0, 34, 34);
}

.button{
  border: none;
  padding: .4rem;
  padding: .4rem;
  max-width: 6rem;
  text-align: center;
  font-size: 1rem;
  
}



a{
  text-decoration: none;
  color: rgb(250, 232, 30);
}

div {
  color: rgb(250, 232, 30);
}



.project{
    height: 500px;
    background: rgb(0, 42, 42);
    position: relative;
    box-shadow:  2px 2px 7px rgb(0, 34, 34);
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.project:nth-of-type(odd) .project-content{
    grid-row-start: 1;
}

.project-content div, h1, a{
    padding:2rem;
   /* display: grid;*/
}
.project h1{
    font-size: 1.2rem;
    color: rgb(255,215,0);
    font-weight: 300;
}

.project p {
    color: whitesmoke;
    padding-bottom: 1rem;
}

.admin-icons{
    position: absolute;
    right: 1rem;
    bottom: 1rem;
    display: grid;
    grid-auto-flow: column;
    justify-content: end;
    grid-gap:1rem;
    gap:1rem;
}

.edit-icons{
    color: rgb(0, 32, 32);
}

.project img {
    width: 100%;
    object-fit: cover;
    object-position: center top;
}

.project.active{
    position: absolute;
    padding: 10vh 20vh;
    top:0;
    left:0;
    z-index:1;
    min-width: 100vw;
    min-height: 100vh;
    -webkit-animation: zoomIn is ease;
            animation: zoomIn is ease;
}

svg{
   color: rgb(250, 232, 30);
   padding-left: .2rem;
}

@-webkit-keyframes zoomIn{
    from{
        -webkit-transform:scale(0.0);
                transform:scale(0.0);
    }
    to{
        -webkit-transform: scale(100vw, 100vh);
                transform: scale(100vw, 100vh);
    }
}

@keyframes zoomIn{
    from{
        -webkit-transform:scale(0.0);
                transform:scale(0.0);
    }
    to{
        -webkit-transform: scale(100vw, 100vh);
                transform: scale(100vw, 100vh);
    }
}

@media (max-width:1000px){
    .project{
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 2fr;
    }
}
.projects-container{
  display: grid;
  grid-gap:1rem;
  gap:1rem;
  padding:0 1rem 1rem 1rem;
  background: rgb(0, 51, 51);
}

.add{
  position: fixed;
  width: 100vw;
  left: 0;
  top: 10vh;
  height: 5vh;
  background-color: rgb(0, 51, 51);
  display: grid;
  place-items: center;
}   

main.project-detail{
    padding:0;
    justify-content: start;
    align-items:start;
}

.parallax-overlay{
    background:rgba(0,0,0,.2);
    width:100vw;
    height:100vh;
    display:grid;
    place-items:center;
    color:rgb(0, 0, 0);
    font-size:2rem;
}

.project-detail .project-content{
    min-height:100vh;
}

.project-content > div, h1{
    color: whitesmoke;
    place-items: center;
    justify-content: center;
}

.project-detail .project-images{
    width:100%;
    display:grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    grid-gap:.5rem;
    gap:.5rem;
}

.project-images img{
    width:100%;
    height:200px;
    object-fit: cover;
}

.pager{
    display:grid;
    grid-auto-flow: column;
    align-items: center;
    justify-content: center;
    grid-gap:4rem;
    gap:4rem;
}

.project .description{
    text-align:left;
}
.lj {
    font-size: 5rem;
}

.omTekst{
    grid-auto-flow: column;
    justify-content: left;
    align-items: left;
    font-size: 1.2rem;
    position: relative;
    padding-bottom: 1rem;
    z-index: 1;
}

.current{
    align-items: left;
}
.header-container{
    position: fixed;
    width: 100vw;
    height: 15vh;
    background: (0, 51, 51, 0.8);
    color: rgb(250, 250, 250);
    display: grid;
    place-items: center;
    z-index: 2;
}

header {
    position: fixed;
    width: 100vw;
    height: 10vh;
    background: rgb(0, 51, 51, 0.8);
    display: grid;
    grid-auto-flow: column;
    justify-content: left;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    z-index: 2;
    padding: 2rem;
}

.burger{
    display: none;
    z-index: 2;
    position: absolute;
    cursor: pointer;
}

header a{
    text-decoration: none;
    font-weight: 400;
    color: rgb(0, 31, 31);
}

[aria-current='page'], .active {
    font-weight: 700;
    color: rgb(250, 232, 30);
}

header img{
    width: 9%;
    z-index: 1;
    cursor: pointer;
}

img{
    padding: 0;
}

@media (max-width:1000px){
    header{
        width: 100%;
        -webkit-transform:translateY(-100%);
                transform:translateY(-100%);
        grid-auto-flow: row;
        align-content: center;
        min-height: 100vh;
        -webkit-transition: .4s ease;
        transition: .4s ease;
    }
    header a{
        font-size: 3rem;
        color: whitesmoke;
    }

    header.visible{
        background-color: rgb(3, 55, 55);
        -webkit-transform:translateY(0);
                transform:translateY(0);
    }
    .burger{
        display: block;
    }
    main{
        padding: 30vh 5vh;
    }

}
main.login{
    align-items: left;
}

.login img {
    width: 200px;
    margin-bottom: 1rem;
    box-shadow:  2px 2px 7px rgb(0, 34, 34);
    border-radius: 50%;
}
input, textarea, label, select, button, .button{
    font-size: 1rem;
    padding: .5rem;
    display: block;
    margin-bottom: 1rem;
    color: rgb(0, 0, 0);
    margin:1rem;
  }
  .edit-icons {
    -webkit-transform:scale(1.5);
            transform:scale(1.5);
  }
  
  .edit-icons{
    color: rgb(0, 0, 0);
    cursor: pointer;
  }

  .project-images{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1 fr));
    grid-gap: .2rem;
    gap: .2rem;
    margin-bottom: 1rem;
    justify-content: left;
  }
  .project-images > div {
    display: grid;
    place-items: center;
    position: relative;
  }
  .project-images > div h3{
    position: relative;
    background-color: rgb(0, 51, 51, 0.8);
    font-size: 1rem;
    color: whitesmoke;
  }

  .project-images img{
    height: 200px;    
    width: 200px;
    display: flex;
    margin-top: 1rem;
  }
  
  input, textarea{
    background-color:  rgb(0, 42, 42);
    border: none;
    box-shadow:  2px 2px 5px rgb(0, 34, 34);
  }
  
  textarea{
    min-width: 50vw;
    min-height: 10rem;
    margin: 1rem;
  }
  
 .edit img{
    width: 5rem;
  }

  
  .edit label{
    padding:0;
    margin-top: 10px;    
  }

.edit {
    -webkit-animation: slideIn 6s ease;
            animation: slideIn 6s ease;
}

.ProsjektDef{
  width: 20vw;
}

@-webkit-keyframes slideIn {
    from{
        -webkit-transform:translateX(100%);
                transform:translateX(100%)
    }
    to{
        -webkit-transform:translateY(0%);
                transform:translateY(0%)
    }
}

@keyframes slideIn {
    from{
        -webkit-transform:translateX(100%);
                transform:translateX(100%)
    }
    to{
        -webkit-transform:translateY(0%);
                transform:translateY(0%)
    }
}

.checkbox{
    display: grid;
    grid-auto-flow: column;
    grid-gap: .1rem;
    gap: .1rem;
    justify-content: start;
    align-items: flex-start;
}
footer {
    width: 100vw;
    height: 20vh;
    background: rgb(0, 42, 42);
    display: grid;
    grid-auto-flow: column;
    justify-content: center;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    z-index: 1;
    padding-left: 2rem;
    margin-top: 2rem;
}

footer a{
    text-decoration: none;
    color: rgb(0, 31, 31);
    font-weight: 400;
}

p{
    color: rgb(255,215,0);
    border-width: 1px;
    height: 2px;
    justify-content: center;
    align-items: center;
}

svg.some-icons{
    color: rgb(255,215,0);
    size: 2rem;
}
